/* eslint global-require: off, no-console: off, promise/always-return: off */

/**
 * This module executes inside of electron's main process. You can start
 * electron renderer process from here and communicate with the other processes
 * through IPC.
 *
 * When running `npm run build` or `npm run build:main`, this file is compiled to
 * `./src/main.js` using webpack. This gives us some performance wins.
 */
import path from 'path';
import { app, BrowserWindow, shell, ipcMain, dialog } from 'electron';
import { autoUpdater } from 'electron-updater';
import log from 'electron-log';
import MenuBuilder from './menu';
import { resolveHtmlPath } from './util';
import { Client } from 'discord-rpc-electron';

let rpc: Client | null = null;
const clientId = '1364237996399788244';
let startTimestamp: number = Date.now();
function setActivity(username: string = '') {
  if (!rpc) return;

  rpc.setActivity({
    details: ' ',
    state: ': ' + username,
    startTimestamp,
    largeImageKey: 'large_image',
    largeImageText: 'CmLauncher',
    smallImageKey: 'small_image',
    smallImageText: 'CmLauncher',
    instance: false,
    buttons: [
      {
        label: '  ',
        url: 'https://cmcraft.su'
      }
    ]
  });
}

class AppUpdater {
  constructor() {
    log.transports.file.level = 'info';
    autoUpdater.logger = log;
    autoUpdater.checkForUpdatesAndNotify();
  }
}

let mainWindow: BrowserWindow | null = null;

ipcMain.on('ipc-example', async (event, arg) => {
  const msgTemplate = (pingPong: string) => `IPC test: ${pingPong}`;
  console.log(msgTemplate(arg));
  event.reply('ipc-example', msgTemplate('pong'));
});

ipcMain.on('window-minimize', () => {
  if (mainWindow) mainWindow.minimize();
});

ipcMain.on('window-maximize', () => {
  if (mainWindow) {
    if (mainWindow.isMaximized()) {
      mainWindow.unmaximize();
    } else {
      mainWindow.maximize();
    }
  }
});

ipcMain.on('window-close', () => {
  if (mainWindow) mainWindow.close();
});

ipcMain.handle('window-is-maximized', () => {
  return mainWindow ? mainWindow.isMaximized() : false;
});

ipcMain.on('get-app-version', (event) => {
  event.reply('app-version', app.getVersion());
});

ipcMain.on('open-external-url', (_event, url) => {
  if (typeof url === 'string' && (url.startsWith('http://') || url.startsWith('https://'))) {
    shell.openExternal(url);
  }
});

ipcMain.on('update-discord-presence', (_event, username) => {
  setActivity(username);
});

ipcMain.handle('dialog:openFile', async () => {
  const { canceled, filePaths } = await dialog.showOpenDialog({
    properties: ['openFile'],
    filters: [{ name: 'Images', extensions: ['jpg', 'png', 'gif', 'jpeg'] }],
  });

  if (canceled) {
    return null;
  }
  return filePaths[0];
});


ipcMain.on('open-devtools', () => {
  if (mainWindow && mainWindow.webContents) {
    mainWindow.webContents.toggleDevTools();
  }
});

ipcMain.handle('select-background-image', async () => {
  if (!mainWindow) {
    return { canceled: true };
  }

  const backgroundsPath = app.isPackaged
    ? path.join(process.resourcesPath, 'assets', 'backgrounds')
    : path.join(__dirname, '../../assets/backgrounds');

  try {
    const fs = require('fs');
    if (!fs.existsSync(backgroundsPath)) {
      fs.mkdirSync(backgroundsPath, { recursive: true });
    }
  } catch (error) {
    console.error('  /  :', error);
  }

  const result = await dialog.showOpenDialog(mainWindow, {
    title: '  ',
    defaultPath: backgroundsPath,
    buttonLabel: '',
    filters: [
      { name: '', extensions: ['jpg', 'jpeg', 'png', 'gif', 'webp'] }
    ],
    properties: ['openFile']
  });

  return result;
});

if (process.env.NODE_ENV === 'production') {
  const sourceMapSupport = require('source-map-support');
  sourceMapSupport.install();
}

const isDebug =
  process.env.NODE_ENV === 'development' || process.env.DEBUG_PROD === 'true';

if (isDebug) {
  require('electron-debug').default();
}

const installExtensions = async () => {
  const installer = require('electron-devtools-installer');
  const forceDownload = !!process.env.UPGRADE_EXTENSIONS;
  const extensions = ['REACT_DEVELOPER_TOOLS'];

  return installer
    .default(
      extensions.map((name) => installer[name]),
      forceDownload,
    )
    .catch(console.log);
};

const createWindow = async () => {
  if (isDebug) {
    await installExtensions();
  }

  const RESOURCES_PATH = app.isPackaged
    ? path.join(process.resourcesPath, 'assets')
    : path.join(__dirname, '../../assets');

  const getAssetPath = (...paths: string[]): string => {
    return path.join(RESOURCES_PATH, ...paths);
  };

  mainWindow = new BrowserWindow({
    show: false,
    width: 1280,
    height: 720,
    minWidth: 1000,
    minHeight: 700,
    icon: getAssetPath('icon.png'),
    webPreferences: {
      preload: app.isPackaged
        ? path.join(__dirname, 'preload.js')
        : path.join(__dirname, '../../.erb/dll/preload.js'),
      contextIsolation: true,
      nodeIntegration: false,
    },
    frame: true,
    transparent: false,
  });

  mainWindow.loadURL(resolveHtmlPath('index.html'));
  
  mainWindow.setMenuBarVisibility(false);

  // devtools with F12
  mainWindow.webContents.on('before-input-event', (event, input) => {
    if (input.key === 'F12') {
      mainWindow?.webContents.toggleDevTools();
    }
  });

  mainWindow.on('maximize', () => {
    if (mainWindow && mainWindow.webContents) {
      mainWindow.webContents.send('window-maximized-change', true);
    }
  });

  mainWindow.on('unmaximize', () => {
    if (mainWindow && mainWindow.webContents) {
      mainWindow.webContents.send('window-maximized-change', false);
    }
  });

  mainWindow.on('ready-to-show', () => {
    if (!mainWindow) {
      throw new Error('"mainWindow" is not defined');
    }
    if (process.env.START_MINIMIZED) {
      mainWindow.minimize();
    } else {
      mainWindow.show();
    }
  });

  mainWindow.on('closed', () => {
    mainWindow = null;
  });

  const menuBuilder = new MenuBuilder(mainWindow);
  menuBuilder.buildMenu();

  // Open urls in the user's browser
  mainWindow.webContents.setWindowOpenHandler((edata) => {
    shell.openExternal(edata.url);
    return { action: 'deny' };
  });

  // Remove this if your app does not use auto updates
  // eslint-disable-next-line
  new AppUpdater();

  rpc = new Client({ transport: 'ipc' });

	rpc.on('ready', () => {
		console.log('Discord RPC ready');
	});

	rpc.login({ clientId }).catch(console.error);


};

/**
 * Add event listeners...
 */

app.on('window-all-closed', () => {
  // Respect the OSX convention of having the application in memory even
  // after all windows have been closed
  if (process.platform !== 'darwin') {
    app.quit();
  }
});

app
  .whenReady()
  .then(() => {
    createWindow();
    app.on('activate', () => {
      // On macOS it's common to re-create a window in the app when the
      // dock icon is clicked and there are no other windows open.
      if (mainWindow === null) createWindow();
    });
  })
  .catch(console.log);

ipcMain.handle('launch-minecraft', async (_event, playerName) => {
  try {
    const { spawn } = require('child_process');
    const fs = require('fs-extra');
    const path = require('path');

    if (!playerName || String(playerName).trim() === '') {
      console.error('    Minecraft');
      return { 
        success: false, 
        error: ',     ' 
      };
    }

    const cleanName = String(playerName).trim();
    console.log(` Minecraft  : "${cleanName}"`);

    let launcherPath;
    if (app.isPackaged) {
      launcherPath = path.join(process.resourcesPath, 'src/MinecraftLauncher/launcher.js');
    } else {
      launcherPath = path.join(__dirname, '../../src/MinecraftLauncher/launcher.js');
    }
    
    console.log(`  : ${launcherPath}`);

    if (!fs.existsSync(launcherPath)) {
      console.error(`     : ${launcherPath}`);

      const altPath1 = path.join(__dirname, '../MinecraftLauncher/launcher.js');
      const altPath2 = path.join(process.cwd(), 'src/MinecraftLauncher/launcher.js');
      
      if (fs.existsSync(altPath1)) {
        console.log(`  : ${altPath1}`);
        launcherPath = altPath1;
      } else if (fs.existsSync(altPath2)) {
        console.log(`  : ${altPath2}`);
        launcherPath = altPath2;
      } else {
        return { success: false, error: `   ` };
      }
    }

    console.log(` : node ${launcherPath} --username "${cleanName}"`);
    
    //   ,   
    const logPath = path.join(path.dirname(launcherPath), 'game', 'minecraft-logs.txt');
    fs.ensureFileSync(logPath);
    if (fs.existsSync(logPath)) {
      //        N 
      fs.writeFileSync(logPath, '---   Minecraft ---\n');
    }
    
    //     IPC
    const child = spawn('node', [launcherPath, '--username', cleanName], {
      detached: true,
      stdio: ['ignore', 'pipe', 'pipe', 'ipc'],
      windowsHide: true
    });
    
    //      minecraft-launcher-core
    let minecraftClientCheck: NodeJS.Timeout;
    let minecraftClientFound = false;
    
    const setupMinecraftLogsForwarding = () => {
      try {
        //   launcher.js 
        const launcherModule = require(launcherPath);
        
        //    
        const minecraftClient = launcherModule.getClient();
        
        if (minecraftClient) {
          console.log('   Minecraft!');
          minecraftClientFound = true;
          
          //      
          if (minecraftClientCheck) {
            clearInterval(minecraftClientCheck);
          }
          
          //     minecraft-launcher-core
          const forwardLogToRenderer = (type: string, message: any) => {
            if (!message) return;
            
            let formattedMessage = message;
            if (typeof message !== 'string') {
              try {
                formattedMessage = JSON.stringify(message);
              } catch (error) {
                formattedMessage = String(message);
              }
            }
            
            const logMessage = `[${type}] ${formattedMessage}`;
            console.log('Forwarding to renderer:', logMessage);
            
            if (mainWindow) {
              mainWindow.webContents.send('minecraft-log', logMessage);
            }
          };
          
          //     
          minecraftClient.on('debug', (e: any) => forwardLogToRenderer('DEBUG', e));
          minecraftClient.on('data', (e: any) => forwardLogToRenderer('DATA', e));
          minecraftClient.on('progress', (e: any) => forwardLogToRenderer('PROGRESS', e ? `${e.type || ''} ${Math.floor(e.percent || 0)}%` : '...'));
          minecraftClient.on('arguments', (e: any) => forwardLogToRenderer('ARGUMENTS', Array.isArray(e) ? e.join(' ') : e));
          minecraftClient.on('error', (e: any) => forwardLogToRenderer('ERROR', e));
          minecraftClient.on('close', (e: any) => forwardLogToRenderer('CLOSE', ` : ${e || 0}`));
        }
      } catch (error) {
        console.error('     :', error);
      }
    };
    
    //      
    setTimeout(setupMinecraftLogsForwarding, 1000);
    
    //  ,     ,   2 
    minecraftClientCheck = setInterval(() => {
      if (!minecraftClientFound) {
        console.log('    Minecraft...');
        setupMinecraftLogsForwarding();
      } else {
        clearInterval(minecraftClientCheck);
      }
    }, 2000);
    
    //    (30 )
    setTimeout(() => {
      if (minecraftClientCheck) {
        clearInterval(minecraftClientCheck);
        if (!minecraftClientFound) {
          console.log('    Minecraft   .');
        }
      }
    }, 30000);
    
    //    stdout  stderr
    child.stdout?.on('data', (data: Buffer) => {
      const logMessage = data.toString().trim();
      console.log('   stdout:', logMessage);
      if (logMessage && mainWindow) {
        mainWindow.webContents.send('minecraft-log', logMessage);
      }
    });
    
    child.stderr?.on('data', (data: Buffer) => {
      const logMessage = data.toString().trim();
      console.log('   stderr:', logMessage);
      if (logMessage && mainWindow) {
        mainWindow.webContents.send('minecraft-log', `[ERROR] ${logMessage}`);
      }
    });
    
    //  IPC    
    child.on('message', (message: { type: string; data: string }) => {
      console.log(' IPC :', message);
      if (message && message.type === 'minecraft-log' && mainWindow) {
        mainWindow.webContents.send('minecraft-log', message.data);
      }
    });
    
    //     
    if (mainWindow) {
      const startMessage = `[${new Date().toISOString()}] Minecraft   : ${cleanName}`;
      console.log('    :', startMessage);
      mainWindow.webContents.send('minecraft-log', startMessage);
    }
    
    //   -
    const watchLogFile = () => {
      try {
        if (fs.existsSync(logPath) && mainWindow) {
          const logContent = fs.readFileSync(logPath, 'utf8');
          const logLines = logContent.split('\n').filter((line: string) => line.trim());
          
          //     ,    
          const lastLogs = logLines.slice(-20);
          
          for (const log of lastLogs) {
            if (log.trim()) {
              mainWindow.webContents.send('minecraft-log', log.trim());
            }
          }
        }
      } catch (error) {
        console.error('   -:', error);
      }
    };
    
    //        2 
    watchLogFile();
    const logWatcher = setInterval(watchLogFile, 2000);
    
    //    
    if (mainWindow) {
      mainWindow.on('closed', () => {
        clearInterval(logWatcher);
        if (minecraftClientCheck) {
          clearInterval(minecraftClientCheck);
        }
      });
    }
    
    //     
    child.unref();
    
    return { success: true, pid: child.pid };
  } catch (error: unknown) {
    console.error('   Minecraft:', error);
    const errorMessage = error instanceof Error ? error.message : String(error);
    return { success: false, error: errorMessage };
  }
});
